$why-we-pl-sm: 130px;
$why-we-pl-xs: 71px;

.b-why-we{
	position: relative;
	padding-left: 130px;
	margin-bottom: 80px;
    
    @include huge{
        margin-bottom: 0;
    }
}
.b-why-we--1{
	padding-left: 122px;
    
    @include note{
        padding-left: $why-we-pl-sm;    
    }    
    @include phone{
        padding-left: $why-we-pl-xs;
    }
}
.b-why-we--2{
	padding-left: 129px;

	@include huge {
		padding-left: 131px;
	}
    @include note{
        padding-left: $why-we-pl-sm;    
    } 
    @include phone{
        padding-left: $why-we-pl-xs;
    }
}
.b-why-we--3{
	padding-left: 160px;

	@include huge {
		padding-left: 145px;
	}
    @include note{
        padding-left: $why-we-pl-sm;    
    } 
    @include phone{
        padding-left: $why-we-pl-xs;
    }
}
.b-why-we__title{
	margin: 0 0 14px;
	font-size: 26px;
	font-weight: 700;
	line-height: 30px;
    letter-spacing: -.01em;
	text-transform: uppercase;
	color: #121212;

	@include huge {
		font-size: 19px;
		line-height: 24px;
		margin-bottom: 10px;
	} 
    @include phone{
        font-size: 16.3px;
        line-height: 20px;
        margin-bottom: 7px;
    }
