//		font-size: 30px;
//		line-height: 30px;
//	}
//	@include fourK {
//		font-size: 35px;
//		line-height: 40px;
//	}
}
.b-why-we__text{
	margin: 0;
	padding-left: 1px;
	padding-right: 0;
	font-size: 16px;
	font-weight: 400;
	line-height: 18px;
	color: #121212;

	@include huge {
		font-size: 14px;
	}
//	@include twoK {
//		font-size: 20px;
//		line-height: 20px;
//	}
//	@include fourK {
//		font-size: 28px;
//		line-height: 35px;
//	}
    @include phone{
        padding-left: 0;
        line-height: 13px;
        font-size: 11.34px;
    }
}
.b-why-we__icon{
	position: absolute;
//	top: 0; left: 0;
