.b-why-we-single{
	max-width: 30%;
    
	@include huge {
		max-width: 33%;
	}
    @include pc{
        max-width: 32%;
    }
    @include note{
        max-width: none;
        min-height: 115px;
        margin-bottom: 30px;
        
        &:last-child{
            margin-bottom: 0;
        }
    }    
    @include phone{
        margin-bottom: 23px;
    }
}
