.b-service{
	margin-bottom: 45px;
    
    @include phone{
        margin: 0 0 23px;
    }
}
.b-service__img{
    display: block;
	margin: 0 0 32px;
    cursor: pointer;
    transition: opacity 0.3s;
    
    &:hover{
        opacity: 0.7;
    }
    
	img{
		display: block;
		margin: 0 auto;
		width: 100%;

//		@include twoK {
//			width: 100%;
//		}
	}
    
    @include phone{
        margin: 0 0 5px;
    }
}
.b-service-separate{
	width: 31.5%;
//	margin-left: 1px;
    @include tablet{
//        width: 100%;
        width: 47%;
        
        &:last-child{
            display: none;
//            margin: 0 auto;
        }
    }
    @include small{
        width: 100%;
        
        &:last-child{
            display: block;
        }
    }
}
.b-service__list{
	margin: 0;
	padding: 0;
	list-style-type: none;
}
.b-service__item{
	padding: 2px 0;
	text-align: center;
    font-size: 21px;
	font-weight: 300;
	line-height: 28px;
	color: #121212;
    
	@include huge {
		font-size: 16px;
		line-height: 21px;
	}
//	@include twoK {
//		font-size: 27px;
//		line-height: 33px;
//	}
//	@include fourK {
//		font-size: 40px;
//		line-height: 45px;
//	}
    @include phone{
        font-size: 12px;
        line-height: 18px;
    }
}

