.scroll{
    display: none;
	position: fixed;
	top: -100%;
	left: 0;
	right: 0;
	z-index: 300;
	transition: top 300ms;
	background-color: #d1d4d3;
	box-shadow: 0 3px 6px rgba(209, 212, 211, 1); 

	&.is--active{
		top: -1px;
	}
}
.scroll__logo{
	float: left;
}
.scroll__nav{
	float: left;
	.b-nav--simple {
		margin-top: 0;
	}
}
.scroll__cart{
	float: right;
	padding-top: 21px;

//	@include twoK{
//		padding-top: 16px;
//	}
}